
.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  transition: 0.5s;
  padding-top: 60px;
}

.sideBar a {
  padding: 5px 8px 8px 32px !important;
  text-decoration: none;
  font-size: 20px;
  color: white;
  display: block;
  transition: 0.3s;
  margin-top: 8px;
}
.active {
  background-color: #2e3342;
  color: white !important;
  border-radius: 0px;
}

.sideBar a:hover {
  color: white;
}

.sideBar .closebtn {
  position: absolute;
  top: 0;
  right: 25px;
  font-size: 36px;
  margin-left: 50px;
}
.sideBar {
  background-color: #383F4F !important;
  /* height: 700px !important; */

}
.w3-xlarge {
    color: white !important;
    position: absolute !important;
    /* margin-top: -10% !important; */
    top: 1%;
    padding-left: 10px !important;
    /* padding-top: 5px !important; */
}

@media screen and (max-height: 450px) {
  .sidenav {padding-top: 15px;}
  .sidenav a {font-size: 18px;}
}
